:root {
  --text: #222;
}

body {
  margin: 0;
  color: var(--text);
  background-color: blueviolet;
  font-size: 50px;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  font-size: 1.5 rem;
}

.hero {
  background-image: url("../images/images.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 300px;
  width: 100%;
}